/**
 * æ–°ãŸã«ã‚¢ãƒ³ãƒ­ãƒƒã‚¯ã•ã‚ŒãŸå®Ÿç¸¾ãŒãªã„ã‹ãƒã‚§ãƒƒã‚¯ã™ã‚‹
 * @param data - ãƒ¦ãƒ¼ã‚¶ãƒ¼ã®å…¨ãƒ­ã‚°ã€å…¨ã‚»ãƒƒã‚·ãƒ§ãƒ³ã€æ—¢ã«æŒã£ã¦ã„ã‚‹å®Ÿç¸¾IDã®ãƒªã‚¹ãƒˆ
 * @returns æ–°ãŸã«ã‚¢ãƒ³ãƒ­ãƒƒã‚¯ã•ã‚ŒãŸå®Ÿç¸¾ã®ãƒªã‚¹ãƒˆ
 */
export const checkAndUnlockAchievements = (data: {
  logs: BohLog[];
  sessions: FocusSession[];
  unlockedIds: string[];
}): AchievementDefinition[] => {
  const newlyUnlocked: AchievementDefinition[] = [];

  for (const achievement of ALL_ACHIEVEMENTS) {
    // ã¾ã ã‚¢ãƒ³ãƒ­ãƒƒã‚¯ã—ã¦ãŠã‚‰ãšã€ã‹ã¤æ¡ä»¶ã‚’æº€ãŸã—ã¦ã„ã‚‹ã‹
    if (
      !data.unlockedIds.includes(achievement.id) &&
      achievement.condition(data)
    ) {
      newlyUnlocked.push(achievement);
    }
  }

  return newlyUnlocked;
};
