import React from "react";
import { NavigationContainer } from "@react-navigation/native";
import { createNativeStackNavigator } from "@react-navigation/native-stack";
import { createBottomTabNavigator } from "@react-navigation/bottom-tabs";

import HomeScreen from "../screens/HomeScreen";
import LogCreationScreen from "../screens/LogCreationScreen";
import AnalysisScreen from "../screens/AnalysisScreen";
import TimerScreen from "../screens/TimerScreen";
import ProfileScreen from "../screens/ProfileScreen";

// Stack Navigatorç”¨ã®å‹å®šç¾©
export type RootStackParamList = {
  Main: undefined; // Tab Navigatorå…¨ä½“
  LogCreation: undefined;
};

// Tab Navigatorç”¨ã®å‹å®šç¾©
export type TabParamList = {
  Home: undefined;
  Analysis: undefined;
  Timer: undefined;
};

const Stack = createNativeStackNavigator<RootStackParamList>();
const Tab = createBottomTabNavigator<TabParamList>();

function HomeStack() {
  return (
    <Stack.Navigator>
      <Stack.Screen
        name="Main"
        component={MainTabs}
        options={{ headerShown: false }}
      />
      <Stack.Screen
        name="LogCreation"
        component={LogCreationScreen}
        options={{ title: "BOH-LOGã‚’è¨˜éŒ²" }}
      />
    </Stack.Navigator>
  );
}

function MainTabs() {
  return (
    <Tab.Navigator screenOptions={{ headerShown: false }}>
      <Tab.Screen
        name="Home"
        component={HomeScreen}
        options={{ title: "ãƒ›ãƒ¼ãƒ " }}
      />
      <Tab.Screen
        name="Analysis"
        component={AnalysisScreen}
        options={{ title: "åˆ†æ" }}
      />
      <Tab.Screen
        name="Timer"
        component={TimerScreen}
        options={{ title: "é›†ä¸­ã‚¿ã‚¤ãƒãƒ¼" }}
      />
      <Tab.Screen
        name="Profile"
        component={ProfileScreen}
        options={{ title: "ãƒ—ãƒ­ãƒ•ã‚£ãƒ¼ãƒ«" }}
      />
    </Tab.Navigator>
  );
}

export default function AppNavigator() {
  return (
    <NavigationContainer>
      <HomeStack />
    </NavigationContainer>
  );
}
