// src/screens/HomeScreen.tsx
import React, { useState, useCallback } from "react";
import { View, Text, StyleSheet, FlatList, Button } from "react-native";
import { useFocusEffect } from "@react-navigation/native";
import { getBohLogs } from "../api/database";
import { BohLog } from "../types";
import BohLogItem from "../components/BohLogItem";
import { NativeStackScreenProps } from "@react-navigation/native-stack";
// import AvatarDisplay from '../components/AvatarDisplay';

// AppNavigator.tsxã§å®šç¾©ã™ã‚‹å‹
type RootStackParamList = {
  Home: undefined;
  LogCreation: undefined;
};
type Props = NativeStackScreenProps<RootStackParamList, "Home">;

export default function HomeScreen({ navigation }: Props) {
  const [logs, setLogs] = useState<BohLog[]>([]);
  const [isLoading, setIsLoading] = useState(true);

  useFocusEffect(
    useCallback(() => {
      const fetchLogs = async () => {
        setIsLoading(true);
        const fetchedLogs = await getBohLogs();
        // æ—¥ä»˜ã®é™é †ã§ã‚½ãƒ¼ãƒˆ
        fetchedLogs.sort(
          (a, b) =>
            new Date(b.timestamp).getTime() - new Date(a.timestamp).getTime()
        );
        setLogs(fetchedLogs);
        setIsLoading(false);
      };
      fetchLogs();
    }, [])
  );

  return (
    <View style={styles.container}>
      {/* <AvatarDisplay state={...} /> */}
      <Text style={styles.header}>æœ€è¿‘ã®BOH-LOG</Text>

      <Button
        title="ã¼ãƒ¼ã£ã¨ã—ãŸæ™‚é–“ã‚’è¨˜éŒ²ã™ã‚‹"
        onPress={() => navigation.navigate("LogCreation")}
      />

      {isLoading ? (
        <Text>èª­ã¿è¾¼ã¿ä¸­...</Text>
      ) : (
        <FlatList
          data={logs}
          keyExtractor={(item) => item.id}
          renderItem={({ item }) => <BohLogItem log={item} />}
          style={styles.list}
        />
      )}
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    padding: 16,
  },
  header: {
    fontSize: 24,
    fontWeight: "bold",
    marginVertical: 16,
  },
  list: {
    marginTop: 16,
  },
});
